import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '../layout/Layout.vue'
import defaultNav from '../layout/components/TopNav.vue'
import Login from '../views/Authenticate/Login.vue'

Vue.use(VueRouter)

export const contantRoutes = [
  {
    path: '/login',
    name: "Login",
    component: Login,
  },
  {
    path: '/',
    component: Layout,
    redirect: '/user',
    children: [
      {
        path: 'dashboard',
        components: {
          default: () => import('@/views/Home.vue'),
          topNav: defaultNav
        },
        props: {
          topNav: {
            title: 'DashBoard'
          }
        },
        name: 'Home',
      }
    ]
  },
  {
    path: '/user',
    component: Layout,
    children: [
      {
        path: '/',
        components: {
          default: () => import('@/views/User/Profile.vue'),
          topNav: defaultNav
        },
        name: 'User',
      },
      {
        path: 'membership',
        components: {
          default: () => import('@/views/User/SubPages/Membership.vue'),
          topNav: defaultNav
        },
        name: 'Membership',
      },
      {
        path: 'orders',
        components: {
          default: () => import('@/views/User/SubPages/Orders.vue'),
          topNav: defaultNav
        },
        name: 'MyOrder',
      },
      {
        path: 'schools',
        components: {
          default: () => import('@/views/User/SubPages/Schools.vue'),
          topNav: defaultNav
        },
        name: 'SchoolSetting',
      },
      {
        path: 'settings',
        components: {
          default: () => import('@/views/User/SubPages/Settings.vue'),
          topNav: defaultNav
        },
        name: 'MySettings',
      },
      {
        path: 'support',
        components: {
          default: () => import('@/views/User/SubPages/Support.vue'),
          topNav: defaultNav
        },
        name: 'CustomerSupport',
      },
    ]
  },
  {
    path: '/quest',
    component: Layout,
    children: [
      {
        path: '/',
        components: {
          default: () => import('@/views/Quest/Home.vue'),
          topNav: defaultNav
        }
      },
      {
        path: 'check',
        components: {
          default: () => import('@/views/Quest/Check.vue'),
          topNav: defaultNav
        },
        props: {
          topNav: {
            title: '',
            backBtn: true
          }
        }
      },
      {
        path: 'course',
        components: {
          default: () => import('@/views/Quest/Course.vue'),
          topNav: defaultNav
        },
        props: {
          topNav: {
            title: '課程',
            backBtn: true
          }
        }
      },
      {
        path: 'timeline',
        components: {
          default: () => import('@/views/Quest/Timeline.vue'),
          topNav: defaultNav
        },
        props: {
          topNav: {
            title: '',
            backBtn: true
          }
        }
      },
      {
        path: 'rollcall',
        components: {
          default: () => import('@/views/Quest/RollCall.vue'),
          topNav: defaultNav
        },
        props: {
          topNav: {
            title: '點名板',
            backBtn: true
          }
        }
      },
      {
        path: 'cleanup',
        components: {
          default: () => import('@/views/Quest/Cleanup.vue'),
          topNav: defaultNav
        },
        props: {
          topNav: {
            title: '課室清潔',
            backBtn: true
          }
        }
      },
      {
        path: 'interview/absent',
        components: {
          default: () => import('@/views/Quest/AbsentInterview.vue'),
          topNav: defaultNav
        },
        props: {
          topNav: {
            title: '出缺席訪談',
            backBtn: true
          }
        }
      },
      {
        path: 'interview/observation',
        components: {
          default: () => import('@/views/Quest/ObservationInterview.vue'),
          topNav: defaultNav
        },
        props: {
          topNav: {
            title: '個別觀察訪談',
            backBtn: true
          }
        }
      },
      {
        path: 'childcare',
        components: {
          default: () => import('@/views/Quest/ChildCare.vue'),
          topNav: defaultNav
        },
        props: {
          topNav: {
            title: '保育任務',
            backBtn: true
          }
        }
      },
    ]
  },
  {
    path: '/achievement',
    component: Layout,
    children: [
      {
        path: '/',
        components: {
          default: () => import('@/views/Achievement/Home.vue'),
          topNav: defaultNav
        }
      },
      {
        path: 'scale',
        components: {
          default: () => import('@/views/Achievement/Scale.vue'),
          topNav: defaultNav
        }
      },
      {
        path: 'super',
        components: {
          default: () => import('@/views/Achievement/Super.vue'),
          topNav: defaultNav
        }
      },
      {
        path: 'statistics',
        components: {
          default: () => import('@/views/Achievement/Statistics.vue'),
          topNav: defaultNav
        }
      },
      {
        path: 'enhancement',
        components: {
          default: () => import('@/views/Achievement/Enhancement.vue'),
          topNav: defaultNav,
        },
        props: {
          default: route => ({ type: route.query.type })
        }
      },
    ]
  },
  {
    path: '/about',
    component: Layout,
    children: [
      {
        path: '/',
        components: {
          default: () => import('@/views/About.vue'),
          topNav: defaultNav
        },
        name: 'About',
      }
    ]
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: contantRoutes
})

export default router
