<template>
  <v-navigation-drawer v-model="isOpen" app>
    <v-list-item>
      <v-list-item-content>
        <v-list-item-title class="title">Baby Bear Care</v-list-item-title>
        <v-list-item-subtitle>Member Toolkit</v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>
    <template v-slot:prepend>
      <v-list-item two-line :to="'/user'">
        <v-list-item-avatar>
          <img src="https://randomuser.me/api/portraits/women/81.jpg" />
        </v-list-item-avatar>

        <v-list-item-content>
          <v-list-item-title>Jane Smith</v-list-item-title>
          <v-list-item-subtitle>Logged In</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
    </template>
    <v-list>
      <v-list-item v-for="item in items" :key="item.title" :to="item.route">
        <v-list-item-icon>
          <v-icon>{{ item.icon }}</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
      <!-- Logout -->
      <v-list-item @click="onLogout">
        <v-list-item-icon>
          <v-icon>mdi-logout</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title>Logout</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      items: [
        { title: "Login", icon: "mdi-view-dashboard", route: "/login" },

        { title: "任務", icon: "mdi-calendar-check", route: "/quest" },
        { title: "成就", icon: "mdi-license", route: "/achievement" },

        { title: "Dashboard", icon: "mdi-view-dashboard", route: "/dashboard" },
        { title: "About", icon: "mdi-help-box", route: "/about" },
      ],
    };
  },
  computed: {
    ...mapGetters("layout", ["drawerOpen"]),
    isOpen: {
      get() {
        return this.drawerOpen;
      },
      set(val) {
        this.toggleDrawer(val);
      },
    },
  },
  methods: {
    ...mapActions("auth", ["logout"]),
    ...mapActions("layout", ["toggleDrawer"]),
    onLogout() {
      this.isOpen = false;
      this.logout();
      this.$router.push("/login");
    },
  },
};
</script>

